import { range, shuffle } from 'lodash';
import { useState, FC, SetStateAction } from 'react';
import { Transform } from 'stream';

const SIZE = 30;
const getArr = () => shuffle(range(1, SIZE + 1))

const swap = (arr, a, b) => {
    const tmp = arr[a]
    arr[a] = arr[b]
    arr[b] = tmp
}

type TSetArr=(value: SetStateAction<number[]>)=> void //alias
const delaySetArr = (arr:number[], setArr:TSetArr)  => {

    return new Promise((resolve)=>{
        setArr(arr)
        setTimeout(()=>resolve(), 50)
    })
}
const sort = async (arr: number[], setArr: TSetArr) => {
    {
    /*  https://en.wikipedia.org/wiki/Insertion_sort
     i ← 1
  while i < length(A)
      j ← i
      while j > 0 and A[j-1] > A[j]
          swap A[j] and A[j-1]
          j ← j - 1
      end while
      i ← i + 1
  end while
    */}
    let i = 1;
    while (i < arr.length) {
        let j = i;
        while (j > 0 && arr[j - 1] > arr[j]) {
            
            swap(arr, j, j - 1)
            await delaySetArr(arr, setArr)
            j = j - 1;
        }
        i = i + 1;
    }
}

//callback hell -> promis -> async/await es7
interface IPropsBar {
    value: number
    idx: number
}
const Bar: FC<IPropsBar> = (props) => {
    const { value, idx } = props
    const style = { height: value * 10, transform: `translateX(${idx * 22}px)` }
    return (
        <>
            <div style={style} className='bar' />

            <style jsx>{`
    .bar{
        position: absolute;
        width: 20px;
        background-color: black;
                }

                `}</style>
        </>
    )
}

export default () => {

    const [arr, setArr] = useState(getArr())

    const handleShuffle = () => {
        setArr(getArr())
    }
    const handleSort = () => {
        // console.log("before sort\n"+arr);
        const sortedArr = [...arr]
        sort(sortedArr,setArr)
        setArr(sortedArr)
        // console.log("after sort\n"+arr);
    }

    return (
        <div>
            <div className='board'>

                {arr.map((value, i) => <Bar key={i} value={value} idx={i} />)}


            </div>
            <div className='index i' style={{ transform: `translateX(30px)`}}>i</div>
            <div className='index j'>j</div>




            <div className='buttonBox'>
                <button onClick={handleShuffle}>shuffle</button>
                <button onClick={handleSort}>sort</button>
            </div>
            <style jsx>{`
            .board{
                width: 100%;
                height: 200px;
                background-color: green;
                color: white;
                transform: rotateX(180deg);
                }
            .buttonBox{ 
               width: 100%;
               height: 60px;
               background-color: gray;        
               text-align:right;
            }
            button{ 
                font-size: 40px;
            }
            .index{
            position: absolute;
            width: 20px;
            background-color: blue;
            opacity: 0.8;
            }
            .index.j{
            background-color: blue;
            color: white;

            }
            .index.i{
            background-color: yellow;
            color: black;

            }
          
        `}</style>
        </div>
    )
}